.stars {
  display: flex;
  list-style-type: none;
  padding: 0;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__star:first-child {
    margin-left: 0;
  }

  &__star:last-child {
    margin-right: 0;
  }

  &--4 :nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
  }
}
